body {
  font-family: 'Roboto', sans-serif;
  margin: 15px;
}

@media only screen and (max-width: 480px) {
  body {
    margin: 0;
  }
}

.out {
  max-width: 1230px;
  margin: 20px auto;
  box-shadow: 0px 1px 57px 0px rgba(0, 0, 0, 0.96);
  border-radius: 5px;
}

a {
  text-decoration: none;
}

button {
  background-color: #fff;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: linear-gradient(0deg, #b4bcc4 0%, #fff 100%);
  border-radius: 5px 5px 0 0;
}

.top-panel .col {
  font-size: 13px;
}

.top-panel .col .icon {
  font-size: 16px;
}

.header {
  display: flex;
  flex-wrap: wrap;
  padding-top: 10px;
  padding-bottom: 8px;
  padding-left: 28px;
  padding-right: 20px;
  justify-content: space-between;
}

@media only screen and (max-width: 633px) {
  .header {
    justify-content: center;
  }
}

.logo-link {
  font-size: 39px;
  font-weight: 400;
  color: #000;
}

.search {
  display: flex;
  align-items: center;
  margin-left: 28px;
  margin-right: 10px;
  flex: 1 0;
}

@media only screen and (max-width: 633px) {
  .search {
    margin-top: 10px;
    margin-bottom: 15px;
    margin-right: 28px;
  }
}

.search__input {
  display: flex;
  flex: 1 0;
}

.search__input-term {
  width: 100%;
  min-width: 125px;
  padding: 8px 0 6px 12px;
  border: 1px solid #dcdee0;
}

.search__input-term::placeholder {
  font-size: 12px;
}

.search__input-btn {
  font-size: 16px;
  padding: 4px 8px 5px 5px;
  margin-left: -5px;
  border: 1px solid #dcdee0;
  transition: 0.5s ease;
}

.search__input-btn:hover {
  background: #c1c5c8;
}

.search__input-btn:hover .icon-search {
  fill: #fff;
}

.search__settings {
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.search__settings a {
  margin-left: 16px;
  font-size: 12px;
  color: #0a4f8b;
}

.user {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 935px) {
  .user {
    display: none;
  }
}

.user__home {
  width: 37px;
  height: 37px;
  background: #edeff1;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin-right: 13px;
}

.user__name {
  font-size: 12px;
  font-weight: 100;
  color: #2e3236;
  letter-spacing: 0.29px;
}

.user__btn {
  margin-left: 12px;
  padding-right: 18px;
}

.btn {
  font-size: 10px;
  font-weight: 100;
  border: 1px solid #dcdee0;
  border-radius: 10px;
  padding: 3px 11px;
  transition: 0.5s ease;
}

.btn:hover {
