}

.mobile-navigation .mobile-nav {
  margin-top: 10px;
}

.mobile-navigation .mobile-nav__list-link {
  display: block;
  padding: 6px;
  color: #383e42;
  text-transform: uppercase;
  transition: 0.5s;
}

.mobile-navigation .mobile-nav__list-link:hover {
  background-color: #dcdee0;
}

.mobile-navigation .btn-menu {
  cursor: pointer;
  text-transform: uppercase;
  border: 1px solid #dcdee0;
  border-radius: 3px;
  padding: 8px 15px;
  transition: 0.3s;
}

.mobile-navigation .btn-menu:hover {
  background-color: #dcdee0;
  color: #fff;
}

.mobile-bg-job {
