.slick-arrow {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.slick-next {
  position: absolute;
  right: 20px;
  bottom: 20px;
}

.slick-next:after {
  content: url(../img/right-arrow.svg);
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 60%);
}

.slick-prev {
  position: absolute;
  right: 58px;
  bottom: 20px;
  z-index: 1;
}

.slick-prev:after {
  content: url(../img/left-arrow.svg);
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 60%);
}

