body {
	// font-family: 'Roboto', sans-serif
	@include r-reg;
	margin: 15px;
	@include r(480) {
		margin: 0; } }
.out {
	max-width: 1230px;
	margin: 20px auto;
	box-shadow: 0px 1px 57px 0px rgba(0, 0, 0, 0.96);
	border-radius: 5px; }
a {
	text-decoration: none; }

button {
	background-color: #fff; }

input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
	display: none; }
